.caseSingle {
  font-family: "Suisse Intl";

  p {
    font-size: 20px;
    line-height: 130%;
  }
}

.caseSingle_header {
  position: relative;
  width: 100%;
  border-bottom: 1px solid black;
  display: flex;
  h2 {
    font-weight: 500;
    font-size: 85px;
    line-height: 85%;
    text-transform: uppercase;
    margin: 0;
    margin-left: 1%;
  }

  p {
    position: absolute;
    width: 5%;
    font-weight: 400;
    font-size: 50px;
    line-height: 85%;
    margin: 0;
    padding: 0;
    margin-left: 60%;
    margin-top: 28px;

    &:first-of-type {
      width: 12%;
      margin-left: 40%;
    }
  }
}

.description {
  width: 45%;
  margin-left: 40%;
  margin-top: 25px;
}

.tags {
  height: 140px;
  font-size: 20px;
  list-style: none;
  width: 34%;
  margin-left: 40%;
  margin-top: 50px;
  padding: 0;
  display: flex;
  box-sizing: border-box;
  justify-content: space-between;

  li {
    margin-bottom: 5px;
  }

  .tags_left {
    width: 40%;
  }

  div {
    width: 40%;
  }
}

.img_main {
  width: 100%;
  margin-top: 50px;
}
